.App-header {
  text-align: center;
  padding: 20px;
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile-pic {
  width: 150px; /* Dimensione dell'immagine */
  height: 150px;
  border-radius: 50%; /* Rende l'immagine tonda */
  margin-right: 20px; /* Spazio tra l'immagine e il testo */
}

.navbar {
  background-color: #ff9800; /* Arancione per la navbar */
  padding: 10px; /* Padding per la navbar */
}

.navbar ul {
  list-style-type: none; /* Rimuove i punti dell'elenco */
  padding: 0; /* Rimuove il padding dell'elenco */
}

.navbar li {
  display: inline; /* Dispone gli elementi in linea */
  margin: 0 15px; /* Margine tra gli elementi */
}

.navbar a {
  text-decoration: none; /* Rimuove la sottolineatura */
  color: white; /* Colore del link */
}

section {
  margin: 20px; /* Margine per le sezioni */
}

h2 {
  color: #4caf50; /* Verde scuro per i titoli delle sezioni */
}

.footer {
  background-color: #2e7d32; /* Verde scuro per il footer */
  color: white; /* Colore del testo nel footer */
  padding: 10px; /* Padding per il footer */
  position: relative; /* Posizionamento relativo per il footer */
  bottom: 0; /* Posiziona il footer in basso */
  width: 100%; /* Larghezza completa */
}

/* Animazioni */
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear; /* Animazione della logo se necessario */
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg); /* Rotazione iniziale */
  }
  to {
    transform: rotate(360deg); /* Rotazione finale */
  }
}
